import {Button} from "antd";
import React, {useContext} from 'react';
import {AdKeywordContext} from "../../../../../../contexts/AdKeywordContext";
import SectionHeader from "../../../../../section/SectionHeader";

function AdKeywordListHeader() {
	const context = useContext(AdKeywordContext);

	return (
		<SectionHeader>
			<h2 className="fz-24 fc-gray-700">  </h2>
			<>
				<Button type="default" className="pink" onClick={() => context.setIsAddKeywordModalOpen(true)}> </Button>
				<Button type="default" className="gray" onClick={() => context.setIsSetBidModalOpen(true)}>  </Button>
			</>
		</SectionHeader>
	);
}

export default AdKeywordListHeader;